const ContactUs = () => {


  const sendEmail: React.FormEventHandler<HTMLFormElement> = (e) => {
    e.preventDefault();

    const form = e.currentTarget;

    emailjs.sendForm('service_1ia0q9b', 'template_6ph2wz5', form, '4qAiP_I0-btjlwpU-')
      .then((result) => {
        window.location.reload();
      })
      .catch((error) => {
        console.log(error.text);
      });
  };

  return (
    <form className="contact-form" onSubmit={sendEmail} style={{
      display: 'flex',
      flexDirection: 'column',
      maxWidth: '300px',
      margin: 'auto',
    }}>
      <div className="grid w-full max-w-sm items-center gap-1.5">
        <Label htmlFor="name">Name</Label>
        <Input type="text" id="name" name="from_name" placeholder="Name" />
        <Label htmlFor="email">Email</Label>
        <Input type="text" id="email" name="reply_to" placeholder="Ihre Email" />
        <Label htmlFor="number">Rckrufnummer</Label>
        <Input type="text" id="number" name="number" placeholder="Ihre Nummer fr unsere Rckmeldung" />
        <Label htmlFor="subject">Betreff</Label>
        <Input type="text" id="subject" name="subject" placeholder="Betreff" />
        <Label htmlFor="msg">Nachricht</Label>
        <Textarea id="msg" name="message" placeholder="Ihre Nachricht an uns" />
        <Button type="submit" value="Send">Zum E-Mail Kontaktformular</Button>
      </div>
    </form>
  );
}



